.to-do-list {
  width: 60%;
  margin: 0 auto;
}

.to-do-list header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list {
  overflow: auto;
  max-height: 80vh;
}

.to-do-item {
  display: flex;
  align-items: center;
  border: 1px solid;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 15px;
}

.to-do-item div:first-child {
  flex: 1;
}

.to-do-item .note {
  margin-bottom: 10px;
}

.add-btn:hover {
  cursor: pointer;
}

.delete-btn {
  border: none;
  background-color: #FF4B4B;
  border-radius: 5px;
  padding: 10px 20px;
  color: white;
}

.delete-btn:hover {
  cursor: pointer;
}

.add-to-do-dialog {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-to-do-dialog .content {
  background-color: white;
  border: none;
  border-radius: 10px;
  min-width: 350px;
  padding: 15px;
}

.add-to-do-dialog .content header {
  margin: 5px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.add-to-do-dialog .content footer {
  display: flex;
  justify-content: flex-end;
}

.add-to-do-dialog .btn {
  margin: 0 5px;
}

.add-to-do-dialog .cancel-btn {
  border: 1px solid;
  background-color: none;
  border-radius: 5px;
  padding: 10px 20px;
  color: black;
}
.add-to-do-dialog .save-btn {
  border: none;
  background-color: #28A745;
  border-radius: 5px;
  padding: 10px 20px;
  color: white;
}

.close-btn:hover {
  cursor: pointer;
}